import Api from '../../api'

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: `/v1/winnerlists`,
        params
      })

      return data
    },

    async claim(context, id) {
      return await Api.request({
        method: 'PUT',
        url: `/v1/winnerlists/${ id }`
      })
    }
  }
}


