import Api from '../../api'

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: `/v1/playerbethistory`,
        params
      })

      return data
    },

    async void(context, id) {
      return await Api.request({ url: `/v1/void/${ id }`})
    }
  }
}


