import Api from '../../api'

const API_ENTITY = `/v1/drawlists`
const API_SEND = `/v1/drawtransaction`

export default {
  namespaced: true,

  actions: {
    async list() {
      let { data } = await Api.request({
        url: API_ENTITY
      })

      return data
    },

    async send(context, data) {
      let options = {
        method: 'POST',
        url: API_SEND,
        data
      }

      return await Api.request(options)
    },
  }
}


