import Vue from 'vue'
import Vuex from 'vuex'
import Api from '../api'

import Config from '../config'
import Utils from '../utils'

// Modules
import bet_history from './modules/bet_history'
import bet from './modules/bet'
import commission from './modules/commission'
import dashboard from './modules/dashboard'
import draws from './modules/draws'
import player from './modules/player'
import subagent from './modules/subagent'
import winners from './modules/winners'

export const store = new Vuex.Store({
  modules: {
    bet_history,
    bet,
    commission,
    dashboard,
    draws,
    player,
    subagent,
    winners,
  },

  state: {
    token: null,
    user: null
  },

  getters: {
    token(state) {
      return state.token
    },

    user(state) {
      return state.user
    }
  },

  mutations: {
    SET_TOKEN(state, token) {
      state.token = token
    },

    SET_USER(state, user) {
      state.user = user
    }
  },

  actions: {
    async login({ commit }, user) {
      let { data }  = await Api.request({
        method: 'POST',
        url: '/v1/login',
        data: user
      })

      let item = Utils.Auth.encrypt(data)

      localStorage.setItem(Config.AUTH_KEY, item)
      location.reload()
    },

    async logout({ commit }) {
      await Api.request({
        url: '/v1/logout'
      })

      commit('SET_TOKEN', null)
      commit('SET_USER', null)

      localStorage.removeItem(Config.AUTH_KEY)
      location.href = ''
    }
  }
})

Vue.prototype.$store = store


