import Vue from 'vue'
import Vuex from 'vuex'

// App for auth
import Auth from './apps/auth'

// App for Main
import Main from './apps/main'
import MainRouter from './apps/main/router'

import Api from './api'
import Config from './config'
import Utils from './utils'

Vue.config.productionTip = false
Vue.use(Vuex)

async function startApp() {
  let auth = localStorage.getItem(Config.AUTH_KEY)

  if (auth) {
    let { user, access_token } = Utils.Auth.decrypt(auth)

    if (user && access_token) {
      new Vue({
        el: '#app',

        router: MainRouter.router,

        async created() {
          require('./store')

          Api.setToken(access_token)

          this.$store.commit('SET_USER', user)
        },

        render(h) {
          return h(Main)
        }
      })
    }
  } else {
    new Vue({
      el: '#app',

      created() {
        require('./store')
      },

      render(h) {
        return h(Auth)
      }
    })
  }
}

startApp()



